import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot } from '@angular/router';
import { AutenticationService } from '../../services/autentication.service';

@Injectable({
	providedIn: 'root'
})
export class ExpeditionRoutesGuard implements CanActivate {
	
	constructor(
		private authenticationService: AutenticationService
	) { }
	
	canActivate(event: ActivatedRouteSnapshot) {
		if( event.routeConfig.path == 'home' ) {
			return this.authenticationService.isLoggedIn();
		}
		return this.authenticationService.isPermitted("expedicao", event.url[0].path);
	}
}
