export class Fila {
    fila: string;
    filaId: number;
    atividade: string;
    atividadeId?: number;
    atividadeCodigoExterno?: number;
    operacao: string;
    operacaoId?: number;
    operacaoCodigoExterno?: number;
    operacaoGrupo: string;
    operacaoGrupoId?: number;
    prioridade: number;
    checked: boolean;
    priorizado: boolean;

    constructor(
        fila?: string,
        filaId?: number,
        atividade?: string,
        atividadeId?: number,
        atividadeCodigoExterno?: number,
        operacao?: string,
        operacaoId?: number,
        operacaoCodigoExterno?: number,
        operacaoGrupo?: string,
        operacaoGrupoId?: number,
        prioridade?: number,
        checked?: boolean,
        priorizado?: boolean
    ) {

        this.fila = fila;
        this.filaId = filaId;
        this.atividade = atividade;
        this.atividadeId = atividadeId;
        this.atividadeCodigoExterno = atividadeCodigoExterno;
        this.operacao = operacao;
        this.operacaoId = operacaoId;
        this.operacaoCodigoExterno = operacaoCodigoExterno;
        this.operacaoGrupo = operacaoGrupo;
        this.operacaoGrupoId = operacaoGrupoId;
        this.prioridade = prioridade;
        this.checked = checked;
        this.priorizado = priorizado;
    }
}